/**
 * @refer https://github.com/assemble/handlebars-helpers#eq
 * @param a
 * @param b
 * @param options
 * @returns {*}
 */
export default function(a, b, options) {
  if (arguments.length === 2) {
    options = b;
    b = options.hash.compare;
  }
  if (a === b) {
    return options.fn(this);
  }
  return options.inverse(this);
};



