import $ from 'jquery';
import './assets/scripts/mock.js'

import './assets/scripts/jquery.rotate.js'

import { getJSONP } from '../_commons/js/ajaxUtil';
import { getDomain, isDev } from '../_commons/js/business/domain';
import { getPropertyParams, getParamByName } from '../_commons/js/urlUtil';
import Cookie from '../_commons/js/js.cookie';
import { iOS, Android } from '../_commons/js/device';

import { CONF } from './assets/scripts/conf';
import { Loading } from './assets/scripts/loading';
import { Modal } from './assets/scripts/modal';

import rootTpl from './assets/tpls/root.hbs';
import modalTpl from './assets/tpls/modal.hbs';
import './assets/style/index.less';

const eleLotteryBtnId = '#lottery-btn';

let rotating = false;

// 当前状态数据
let currentData = {};

/**
 * 获取主界面数据
 * @returns {Promise}
 */
const getMainData = () => {

  const domain = getDomain('http://api.',
    'ffan.com/mop/mopng/v1/moonfestival/chance');

  return getJSONP(domain);
};

/**
 * 渲染主界面
 * @param data
 * @returns {*|jQuery}
 */
const renderMainPage = (data) => {
  return $('#root').html(rootTpl(data)).promise();
};


/**
 * 点击抽奖
 * @returns {Promise}
 */
const getLotteryData = () => {
  const domain = getDomain('http://api.', 'ffan.com/mop/mopng/v1/moonfestival/lottery');
  return getJSONP(domain, {});
};

/**
 * 获取旋转到第几个
 * 0:10 , 1:20, 2:30, 3:not, 4:300
 * @param data
 */
const getIndex = (data) => {
  console.log('data', data);
  if (data.type == "COU") {
    if (data.data.origPrice == '10.00') {
      return 0
    }
    if (data.data.origPrice == '20.00') {
      return 1
    }
    if (data.data.origPrice == '30.00') {
      return 2
    }
    if (data.data.origPrice == '300.00') {
      return 4
    }
  }
  return 3;
};

/**
 * 绑定事件
 */
const bindEvent = () => {

  // 抽奖按钮
  $(".lottery-btn").on('click', function (e) {
    const self = $(this);

    self.addClass('disabled');
    const loadInst = new Loading();
    getLotteryData().then(function (data) {
      currentData.lotteryCount = data.lotteryCount;
      //currentData.lotteryState = 0;
      $('.residue-times-num').html(currentData.lotteryCount);
      let index = getIndex(data);
      startRotate(index, data);
    }).fail(function (e) {
      new Loading({
        autoHide: true,
        content: '操作失败，请重试！' + (e.message ? e.message : '')
      });
    }).always(function () {
      loadInst.hide();
      self.removeClass('disabled');
    });
  });

};

/**
 * 抽奖
 */

const rotateFn = (item, data) => {
  let angles = item * 72 + 36;
  $('#reward-spinner').stopRotate();
  $('#reward-spinner').rotate({
    angle: 0,
    animateTo: angles + 3600,
    duration: 10000,
    callback: function () {
      let background = 'normal';
      let reward = false;
      let coupon_code = '';
      let msg = '你离大奖仅一步之遥，<br/>再试试吧！';
      let confirm_btn = true;

      if (item != 3) {
        background = 'zhongbg';
        reward = true;
        confirm_btn = false;
        coupon_code = data.data.productNo;
        msg = `恭喜抽中${data.reward_name}`
      }else {
        if (currentData.lotteryCount == 0) {
          msg = '未抽中哦，<br/>再接再厉吧！<br/>'
        }
      }

      Modal({
        id: '#my-popup',
        template: modalTpl({
          reward: reward,
          confirm_btn: confirm_btn,
          background: background,
          msg: msg
        })
      }).show();
      rotating = !rotating;
    }
  });
};

/**
 * 旋转转盘
 * @param text
 *
 */
function startRotate(index, data) {
  //1:36 2:108 3:180 4:252 5:324
  if (rotating)return;
  rotating = !rotating;
  rotateFn(index, data);
}

const getSignBtnClass = ({ activityStatus, signState }) => {
  var STYLE = CONF.SIGN_BTN_CLASS;
  var ACT = CONF.ACT_STATUS;

  if (activityStatus == ACT.PRESTART) {
    return STYLE.PRE_START;
  }

  if (activityStatus == ACT.OVER) {
    return STYLE.OVER;
  }

  if (activityStatus == ACT.START) {
    if (signState) {
      return STYLE.AFTER_SIGN;
    } else {
      return STYLE.PRE_SIGN;
    }
  }

  return '';
};
const getBindBtnClass = ({ activityStatus, bindCardState }) => {
  console.log('activityStatus' + activityStatus + 'bindCardState' + bindCardState)
  var STYLE = CONF.SIGN_BTN_CLASS;
  var ACT = CONF.ACT_STATUS;

  if (activityStatus == ACT.PRESTART) {
    return STYLE.PRE_START;
  }

  if (activityStatus == ACT.OVER) {
    return STYLE.OVER;
  }

  if (activityStatus == ACT.START) {
    if (bindCardState == 1 || bindCardState == 2) {
      return STYLE.AFTER_SIGN;
    } else {
      return STYLE.PRE_SIGN;
    }
  }

  return '';
};
/**
 * 初始化
 */
$(async function init() {
  try {
    const rootData = await getMainData();
    if(typeof rootData.bindCardState == "string"){
      rootData.bindCardState = parseInt(rootData.bindCardState);
    }
    currentData = rootData;
    await renderMainPage({
      ...rootData,
      signBtnClass: getSignBtnClass(rootData),
      bindBtnClass: getBindBtnClass(rootData)
    });
    await bindEvent();
  } catch (e) {
    console.log('e', e);
    /*new Loading({
      autoHide: true,
      content: (e.message || '服务器有点忙~')
    });*/
  }
});



