import $ from 'jquery';

$.fn.redraw = function() {
  return this.each(function() {
    /* eslint-disable */
    var redraw = this.offsetHeight;
    /* eslint-enable */
  });
};


var mask = null;
var maskActiveClass = 'mask-modal-active';
var modalActiveClass = 'modal-active';
var modalOutClass = 'modal-out';

export function Modal (conf) {
	if(!(this instanceof Modal)) return new Modal(conf);
	if(!conf.id) throw Error("lack of mdoal id.");
	this.el = conf.id ? $(conf.id) : $('body');
	this.tpl = conf.template;
	this.el.html(this.tpl).hide();
	this.bindEvents();
}


var fn = Modal.prototype;

fn.show = function () {
	if(!mask) {
		mask = $('<div class="mask-modal"></div>').appendTo('body');
	}
	if(!mask.hasClass(maskActiveClass)) {
		mask.addClass(maskActiveClass);
	}
	this.el.show().redraw().addClass(modalActiveClass);
	return this;
}

fn.close = function () {
	this.el.removeClass(modalActiveClass).addClass(modalOutClass);
	setTimeout(function (){
		this.el.hide().removeClass(modalOutClass);
		if(mask) {
			mask.removeClass(maskActiveClass);
		}
	}.bind(this), 300);
	return this;
}

fn.destroy = function () {
	this.close().remove();
	return null;
}


fn.bindEvents = function (seletor){
	this.el.on('click', '.close', function () {
		this.close();
	}.bind(this))
}



