import $ from 'jquery';

// 默认参数
var defaults = {
		el: 'body',
    content: '加载中...',
    autoHide: false,
    timeout: 1
};
var template = '<div class="global-loader">{{content}}</div>';

export function Loading(option) {
		const conf = $.extend({}, defaults, option || {});
		this.el = render(conf.el, template.replace(/{{content}}/g, conf.content));
		this.show();
		if(conf.autoHide) {
			setTimeout(()=>{
				this.hide();
			}, conf.timeout*1000);
		}
}

const render = (el, content) => {
	return $(content).appendTo(el);
}

Loading.prototype = {
    show: function() {
        this.el.show();
    },
    hide: function() {
    		this && this.el && this.el.remove();
    }
}



